import React from "react";
import Slider from "react-slick";

const assetPrefix = process.env.NODE_ENV === 'production' ? '/apeservice' : '';

class ShowCase extends React.Component {
    constructor(props) {
        super(props);

    }
    gellery = () => {
        const { projectFolderName, imgLength } = this.props;

        let gallery = [];
        for (let i = 1; i <= imgLength; i++) {
            const imgPath = assetPrefix + `/static/assets/showcase/${projectFolderName}/` + i + '.jpg';
            gallery.push(<div key={"showcase" + projectFolderName} className="col-12 col-md-6 col-lg-4"><img className="img-fluid" key={imgPath} src={imgPath} alt='' /></div>);
        }
        console.log({ gallery })
        return gallery;
    }

    render() {
        const settings = {
            className: "center",
            // centerMode: true,
            infinite: true,
            centerPadding: "0px",
            slidesToShow: 3,
            speed: 500,
            rows: 1,
            slidesPerRow: 2,
            autoplay: true,
            arrows: false,
            // slidesToScroll: 1
        };
        const { projectName, subtitle, content } = this.props;
        return (
            <div className="container">
                <div className="row">
                    <div className="col py-5">
                        <h3>
                            {projectName}
                        </h3>
                        <h4 className="mt-2">{subtitle}</h4>
                        <p className="mt-3">
                            {content}
                            {this.props.children}
                        </p>
                        {/* <div className="row no-gutters"> */}
                        <Slider {...settings}>
                            {this.gellery()}
                        </Slider>
                        {/* </div> */}
                    </div>
                </div>
            </div>
        );
    }
}

export default ShowCase;
